* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: rgb(0, 0, 0);
  font-family: Arial, Helvetica, sans-serif;
  -webkit-user-select: none;
  -webkit-touch-callout: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.main {
  margin-left: auto;
  margin-right: auto;
  background-color: blue;
  overflow: hidden;
}

.main-index {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  margin-left: auto;
  margin-right: auto;
  background-color: brown;
  overflow: hidden;
  background-image: url(../public/images/background-image1.PNG);
  background-size: cover;
  background-position: center;
}

.glass {
  background: rgba(67, 65, 65, 0.5);
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(0px);
  border-radius: 10px;
}

.welcome {
  width: 70%;
  height: 3rem;
  padding-top: 0.9rem;
  border-radius: 2rem;
  text-align: center;
  font-size: 1.2rem;
  color: white;
  margin-left: auto;
  margin-right: auto;
  margin-top: 2rem;
  background: rgba(107, 123, 190, 0.4);
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  border-radius: 10px;
}

.icons-box {
  display: flex;
  width: 70%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 0.5rem;
  justify-content: space-around;
  text-align: center;
}

.icon-insta {
  width: 2rem;
  height: 2rem;
  padding: 0.5rem;
  border-radius: 2rem;
}

.icon-insta:hover {
  background: rgba(74, 74, 74, 0.697);
  box-shadow: 0 8px 1rem 0 rgb(99, 99, 99);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
}

.icons {
  width: 1.6rem;
  height: 1.6rem;
  padding: 0.7rem;
  padding-left: 0.7rem;
  border-radius: 2rem;
}

.icons:hover {
  background: rgba(74, 74, 74, 0.697);
  box-shadow: 0 8px 1rem 0 rgb(0, 0, 0);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);

}

.fa-instagram {
  color: rgb(129, 129, 129);
}

.fa-phone {
  color: rgb(129, 129, 129);
}

.fa-paper-plane {
  width: 1.7rem;
  color: rgb(129, 129, 129);
  padding: auto;
  padding-left: 0.6rem;
}

.fa-envelope {
  color: rgb(129, 129, 129);
}

.journey {
  width: 70%;
  height: 3rem;
  padding-top: 0.9rem;
  border-radius: 2rem;
  text-align: center;
  font-size: 1.2rem;
  color: white;
  margin-left: auto;
  margin-right: auto;
  margin-top: 1rem;
  margin-bottom: 0px;
  padding-bottom: 0px;
}

link {
  text-decoration: none;
}

.journey:hover {
  background: rgba(176, 71, 172, 0.4);
  box-shadow: 0 8px 1rem 0 rgb(137, 101, 138);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
}

.journey:active {
  background: rgba(176, 71, 172, 0.4);
  box-shadow: 0 8px 1rem 0 rgb(137, 101, 138);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
}

/* CONTENT */

.content {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
  justify-content: space-between;
  width: 80%;
  height: 31rem;
  margin-left: auto;
  margin-right: auto;
  margin-top:2rem;
  font-size: 1.3rem;
  color: white;
  padding: 1rem;
  text-shadow: 1px 1px 1rem rgb(255, 255, 255);
}

.content-button {
  width: 75%;
  height: 2rem;
  padding-top: 0.4rem;
  border-radius: 1rem;
  text-align: center;
  font-size: 1.1rem;
  color: rgb(255, 255, 255);
  margin-left: auto;
  margin-right: auto;
  margin-top: 1rem;
  margin-bottom: 0px;
  padding-bottom: 0px;
  background: rgba(13, 13, 13, 0);
}
.content-button:active {

  background: rgba(13, 13, 13, 0.4);
  box-shadow: 0 8px 1rem 0 rgb(0, 0, 0);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
}

.flex {
  width: 60%;
  padding-top: 0.2rem;
  padding-left: 0.5rem;
  display: flex;
  margin-top: 0.3rem;
  margin-left: auto;
  margin-right: auto
}

.small-icon {
  width: 1rem;
  height: 1rem;
}


.home-btn {
  width: 75%;
  height: 1.5rem;
  margin-top: 1rem;
  padding: 0.5rem;
}

.fa-house {
  color: rgb(255, 255, 255);
}

.home-btn:hover {
  background-color: rgb(123, 123, 123);
}

